body {
  margin: 0;
  background-color: rgb(184, 184, 184);
  color: #2a2a2a;
  line-height: 1.5;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}


h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}

button:focus {
  outline: none;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}